  .box {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  .diamond {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 2;
    width: 50px;
    height: 50px;
    border-radius: 5px;
    border: 1px solid #ffffff;
    box-shadow: 1px 1px 0px rgba(0,0,0,0.2);
    background-image: -webkit-linear-gradient(bottom right, #ddd 0%, #fff 100%);
    background-image: linear-gradient(to top left, #ddd 0%, #fff 100%);
    text-align: center;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: rotateZ(45deg);
            transform: rotateZ(45deg);
    -webkit-transition: box-shadow 250ms, -webkit-transform 250ms;
    transition: box-shadow 250ms, -webkit-transform 250ms;
    transition: box-shadow 250ms, transform 250ms;
    transition: box-shadow 250ms, transform 250ms, -webkit-transform 250ms;
  }
  
  .diamond span {
    font-weight: bold;
    -webkit-transform: rotateZ(-45deg);
            transform: rotateZ(-45deg)
  }
  
  .grid .item:hover .diamond,
  .grid .item:focus .diamond {
    box-shadow: 10px 2px 2px rgba(0,0,0,0.2);
    -webkit-transform:  rotateZ(45deg) scale(1.2);
            transform:  rotateZ(45deg) scale(1.2);
  }
  
  .tooltip {
    position: absolute;
    z-index: 1;
    padding: 0.25rem 1rem;
    border-radius: 5px;
    background: #29343d;
    opacity: 0;
    color: #cedfe9;
    font-size: 0.875rem;
    -webkit-transition: opacity 500ms, -webkit-transform 500ms;
    transition: opacity 500ms, -webkit-transform 500ms;
    transition: opacity 500ms, transform 500ms;
    transition: opacity 500ms, transform 500ms, -webkit-transform 500ms;
  }
  
  .tooltip:before {
    content: '';
    position: absolute;
    top: -9px;
    left: calc(50% - 10px);
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #29343d;
  }
  
  .grid .item:hover .tooltip,
  .grid .item:focus .tooltip {
    opacity: 1;
    -webkit-transform: translateY(70px);
            transform: translateY(70px);
  }
  
  .grid .item {
    position: relative;
    padding: 1vw;
    background-position:  left bottom;
    background-size: 150% 100%;
    -webkit-transition: background 500ms, outline 500ms;
    transition: background 500ms, outline 500ms;
    background-position: top left;
    outline: transparent;
  }
  
  .grid .item:hover,
  .grid .item:focus {
    background-position: top right;
  }
  
  .grid .item:focus {
    outline: 2px solid #3eabfa;
  }
  
